import _ from 'lodash';
import React, { Component, Fragment } from 'react';
import PropTypes from 'prop-types';
import {
  Nav,
  NavItem,
  NavLink,
  TabContent,
  TabPane,
  Collapse,
  CardBody,
  Card,
} from 'reactstrap';

// import arrow
import Arrow from 'assets/images/forms/ArrowGradient.svg';

import './Faq.scss';

class QnA extends Component {
  constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);
    this.toggleCollapse = this.toggleCollapse.bind(this);
    this.state = {
      activeTab: null,
      collapse: false,
    };
  }
  toggleCollapse() {
    this.setState({ collapse: !this.state.collapse });
  }
  toggle(tab) {
    const { activeTab } = this.state;
    this.setState({
      activeTab: activeTab === tab ? null : tab,
    });
  }
  // renderBadges() {
  //   const {
  //     data,
  //   } = this.props;
  //   const badge = _.map(data.content, (faq) => {
  //     if (faq.image) {
  //       _.map(faq.image, (img, v) => (
  //         <div>
  //           <img src={img.src} alt={`${img.title} + ${v}`} />
  //         </div>
  //       ));
  //     }
  //     return null;
  //   });
  //   return badge;
  // }
  renderQnA() {
    const { activeTab } = this.state;
    const { data } = this.props;

    const qna = _.map(data.content, (faq, index) => {
      const isActive = activeTab === index;
      const activeStyle = isActive ? 'active' : null;
      const rotateArrowStyle = isActive ? 'rotate-arrow-down' : 'rotate-arrow-up';

      return (
        <Fragment key={index}>
          <Nav
            tabs
            className="qna-container"
          >
            <NavItem onClick={() => { this.toggle(index); }} style={{ flex: 1, position: 'relative' }}>
              <NavLink
                className={activeStyle}
              >
                {faq.question}
              </NavLink>
              <img src={Arrow} className={`${rotateArrowStyle} arrow`} alt="arrow" />
            </NavItem>
          </Nav>
          <TabContent activeTab={activeTab} className="faq-inner">
            <TabPane tabId={index}>
              <Collapse isOpen={isActive}>
                <Card>
                  <CardBody dangerouslySetInnerHTML={{ __html: faq.answer }} />
                </Card>
              </Collapse>
            </TabPane>
          </TabContent>
        </Fragment>
      );
    });
    return qna;
  }
  render() {
    return (
      <Fragment>
        {this.renderQnA()}
      </Fragment>
    );
  }
}
QnA.propTypes = {
  data: PropTypes.object.isRequired,
};

export default QnA;
