import window from 'global/window';
import _ from 'lodash';
import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import Page from 'components/Page/Page';

import FaqData from './FaqData';
import QnA from './QnA';

// Footer
import FooterContainer from '../../containers/FooterContainer';

import './Faq.scss';

import {
  onPageBusiness,
  setStateBusiness,
  onPageMore,
  setStateMore,
  onPageInsurance,
  setStateInsurance,
} from '../../actions/navActions';

class Faq extends Component {
  constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);
    this.toggleCollapse = this.toggleCollapse.bind(this);

    this.state = {
      activeTab: '1',
      collapse: false,
    };

    const pageState = onPageMore.ACTIVE;
    const resetInsurance = onPageInsurance.NONE;
    const resetBusiness = onPageBusiness.NONE;
    const {
      setPage,
      resetIndicatorInsurance,
      resetIndicatorBusiness,
    } = this.props;
    setPage(pageState);
    resetIndicatorInsurance(resetInsurance);
    resetIndicatorBusiness(resetBusiness);
  }
  componentDidMount() {
    window.scroll(0, 0);
  }
  toggleCollapse() {
    this.setState({ collapse: !this.state.collapse });
  }
  toggle(tab) {
    if (this.state.activeTab !== tab) {
      this.setState({
        activeTab: tab,
      });
    }
  }
  renderFaq() {
    return (
      <div className="content-row">
        {
        _.map(FaqData.category, (data, i) => (
          <div className="faq-wrapper" key={i}>
            <div className="title-sec">{data.title}</div>
            <QnA
              index={i}
              data={data}
              activeTab={this.state.activeTab}
              key={i}
            />
          </div>
        ))
      }
      </div>
    );
  }
  render() {
    return (
      <Page id="faq">
        <div className="faq">
          <div className="title-container">
            <div className="title">Frequently Asked Questions</div>
            <div className="desc">
              Here are some of our most frequently asked questions that we commonly receive from our users.
              <br /> For more information about PolicyPal, or if you need support, please contact us via email at support@policypal.com </div>
          </div>
          <div className="content">
            <div className="content-section">
              {this.renderFaq()}
            </div>
          </div>
          <FooterContainer />
        </div>
      </Page>
    );
  }
}

Faq.defaultProps = {
  setPage: null,
  resetIndicatorInsurance: null,
  resetIndicatorBusiness: null,
};

Faq.propTypes = {
  setPage: PropTypes.func,
  resetIndicatorInsurance: PropTypes.func,
  resetIndicatorBusiness: PropTypes.func,
};

const mapDispatchToProps = dispatch => ({
  setPage: (pageState) => {
    dispatch(setStateMore(pageState));
  },
  resetIndicatorInsurance: (resetInsurance) => {
    dispatch(setStateInsurance(resetInsurance));
  },
  resetIndicatorBusiness: (resetBusiness) => {
    dispatch(setStateBusiness(resetBusiness));
  },
});

export default connect(null, mapDispatchToProps)(Faq);
